<template>
  <div>
    <!-- 商品三级分类列表 -->
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread:面包屑带有×的结构-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!-- 分类的面包屑 -->
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName
              }}<i @click="removeCategoryName">×</i>
            </li>
            <!-- 关键字的面包屑 -->
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}<i @click="removeKeyword">×</i>
            </li>
            <!-- 品牌的面包屑 -->
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1]
              }}<i @click="removeTrademark">×</i>
            </li>
            <!-- 平台的售卖属性的面包屑 -->
            <li
              class="with-x"
              v-for="(attrValue, index) in searchParams.props"
              :key="index"
            >
              {{ attrValue.split(":")[1] }}<i @click="removeAttr(index)">×</i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="cahngOrder(1)">
                  <a>综合<span v-show="isOne"><span v-if="isAsc">↑</span><span v-if="isDesc">↓</span></span></a>
                </li>
                <li :class="{ active: isTwo }" @click="cahngOrder(2)">
                  <a>价格<span v-show="isTwo"><span v-if="isAsc">↑</span><span v-if="isDesc">↓</span></span></a>
                </li>
              </ul>
            </div>
          </div>

          <!-- 销售商品列表 -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li
                class="yui3-u-1-5"
                v-for="(goods, index) in goodsList"
                :key="goods.id"
              >
                <div class="list-wrap">
                  <div class="p-img">
                    <!-- 在路由跳转的时候切记要带params参数(goods.id) -->
                    <router-link :to="`/detail/${goods.id}`">
                      <img v-lazy="goods.defaultImg" />
                    </router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥ </em>
                      <i>{{ goods.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                      href="item.html"
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                      >{{ goods.title }}</a
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页器 -->

          <Pagination :pageNo="searchParams.pageNo" :pageSize="searchParams.pageSize" :total="total" :continues="5" 
          @getPageNo="getPageNo"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { mapGetters,mapState } from "vuex";
export default {
  name: "Search",
  components: {
    SearchSelector,
  },
  data() {
    return {
      //带给服务器参数
      searchParams: {
        //一级分类id
        category1id: "",
        //二级分类id
        category2Id: "",
        //三级分类id
        category3Id: "",
        //分类名字
        categoryName: "",
        //关键字
        keyword: "",
        //平台售卖属性操作带的参数
        props: [],
        //品牌
        trademark: "",
        //排序  要有初始值：综合降序
        order: "1:desc",
        //分页器用的：代表的是当前是第几页
        pageNo: 1,
        //代表每一页展示多少条数据
        pageSize: 10,
      },
    };
  },
  //当组件挂载之前执行【整理修改searchParams参数】
  beforeMount() {
    //在发送请求之前，在接口需要传递之前，进行整理（在给服务器发送请求之前，把参数整理好，服务器就会返回查询的数据）
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
    // console.log(this.searchParams);
  },
  mounted() {
    this.getData();
  },
  computed: {
    //mapGetters里面的写法：传递的数据，因为getters计算是没有划分模块【】
    ...mapGetters(["goodsList"]),
    isOne() {
      return this.searchParams.order.indexOf("1") != -1;
    },
    isTwo() {
      return this.searchParams.order.indexOf("2") != -1;
    },
    isAsc(){
      return this.searchParams.order.indexOf("asc")!=-1;
    },
    isDesc(){
      return this.searchParams.order.indexOf("desc")!=-1;
    },
    //获取search模块展示产品一共多少数据
    ...mapState({
      total:state=>state.search.searchList.total
    })
  },
  methods: {
    getData() {
      //向服务器发送请求获取search模块数据（根据不同参数获取不同数据进行展示）
      //所以把请求定义为一个函数，当你需要的时候就可以调用
      this.$store.dispatch("getSearchList", this.searchParams);
    },
    //删除分类名字
    removeCategoryName() {
      //把带给服务器的参数置空了，但是还要向服务器发送请求
      this.searchParams.categoryName = undefined;
      //带给服务器的请求参数都是可有可无的
      //每一次请求前，应该把相应的1，2，3级分类的id置空，让他接受下一次的相应1，2，3id
      //把属性值置空的话还会带给服务器，变为undefined这个字段就不会带给服务器
      this.searchParams.category1id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      // 因为路由跳转会被watch检测到所以不需要调用
      // this.getData();
      //地址栏也需要改变，进行路由跳转
      //将params参数保留(这个if判断一直为true可以删除)
      if (this.$route.params) {
        this.$router.push({ name: "search", params: this.$route.params });
      }
    },
    //删除关键字
    removeKeyword() {
      //给服务器的参数keyword置空
      this.searchParams.keyword = undefined;
      //再次发送请求
      // 因为路由跳转会被watch检测到所以不需要调用
      // this.getData();
      //通知兄弟组件Header利用全局事件总线清除keyword关键字
      this.$bus.$emit("clear");
      //进行路由跳转（自己跳自己）
      //将query参数保留(这个if判断一直为true可以删除)
      if (this.$route.params) {
        this.$router.push({ name: "search", query: this.$route.query });
      }
    },
    //自定义事件回调
    trademarkInfo(trademark) {
      // console.log(trademark);
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      //发送请求
      this.getData();
    },
    //删除品牌
    removeTrademark() {
      //先置空
      this.searchParams.trademark = undefined;
      //再次发送请求
      this.getData();
    },
    //收集平台属性的回调函数（自定义函数）
    attrInfo(attr, attrValue) {
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}`;
      if (this.searchParams.props.indexOf(props) == -1)
        this.searchParams.props.push(props);
      //再次发送请求
      this.getData();
    },
    //removeAttr删除售卖属性
    removeAttr(index) {
      //点谁删除谁
      this.searchParams.props.splice(index, 1);
      //再次发送请求
      this.getData();
    },
    //商品排列的操作
    cahngOrder(flag){
      //获取当前order值
      let originOrder = this.searchParams.order;
      let originFlag = originOrder.split(':')[0];
      let orignSort = originOrder.split(':')[1];
      let newOrder = '';
      //点击相同的按钮时
      if(flag == originFlag){
        newOrder = `${originFlag}:${orignSort=="desc"?"asc":"desc"}`
      }else{
        newOrder = `${flag}:${'desc'}`
      }
      //将新的order赋予searchParams
      this.searchParams.order = newOrder;
      //再发请求
      this.getData();
    },
    //这个是自定义事件的回调函数--获取当前点击的第几页
    getPageNo(pageNo){
      //整理带给服务器的参数
      this.searchParams.pageNo = pageNo;
      //再次发送请求
      this.getData();
    }
  },
  //数据监听：用于监听组件实例身上属性的属性值变化
  watch: {
    //监听路由的信息是否发生变化，如果发生变化，再次发起请求
    $route(newValue, oldValue) {
      //每一次请求前，应该把相应的1，2，3级分类的id置空，让他接受下一次的相应1，2，3id
      this.searchParams.category1id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //再次发起请求之前，再次整合params参数和query参数
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      // console.log(this.searchParams);
      this.getData();
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: -5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      
    }
  }
}
</style>